html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  font-family: "Raleway", sans-serif;
  font-size: 1.6rem;
  line-height: 1.2;
}

p {
  color: #000;
  line-height: 1.8;
}

[class*=__contenedor] {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Staatliches", cursive;
  color: #484349;
  margin: 0;
}

h1 {
  font-size: 4.4rem;
  font-weight: 700;
}

h2 {
  font-size: 4rem;
}

h1, h2 {
  font-family: "Staatliches", cursive;
  text-align: center;
}

h3 {
  font-size: 2.4rem;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

.barra {
  padding: 2rem 0;
}
.barra__img {
  margin: 0 auto;
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .barra .barra__img {
    margin: unset;
  }
}

.menu {
  text-align: center;
}
@media (min-width: 768px) {
  .menu ul {
    display: flex;
    align-items: center;
    gap: 2rem;
  }
}
.menu li {
  margin-bottom: 1.5rem;
}
@media (min-width: 768px) {
  .menu li {
    margin-bottom: 0;
  }
}
.menu__element {
  font-size: 1.4rem;
  color: #484349;
  text-transform: capitalize;
}
.menu__element--active {
  font-weight: 700;
  color: #b8336a;
}
@media (min-width: 768px) {
  .menu__element--active {
    display: flex;
    align-items: center;
  }
}
@media (min-width: 768px) {
  .menu__element--active::after {
    content: "";
    display: inline-block;
    height: 4rem;
    margin: 0 0 0 2rem;
    border-right: 2px solid rgb(242, 242, 242);
  }
}

.avif .hero {
  background-image: url("../img/hero.avif");
}

.webp.noavif .hero {
  background-image: url("../img/hero.webp");
}

.noavif.nowebp .hero {
  background-image: url("../img/hero.jpg");
}

.hero {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 60rem;
}
.hero__info {
  max-width: 60rem;
}
.hero__titulo {
  color: #FFF;
  text-align: center;
  margin: 0;
  line-height: 1.2;
}
.hero__btn {
  background-color: #ff8015;
  font-size: 2rem;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFF;
  text-decoration: none;
  border: none;
  margin-top: 2rem;
  padding: 1.5rem 4rem;
  text-transform: unset;
  font-weight: 700;
  transition: 0.3s background-color;
}
.hero__btn:hover {
  cursor: pointer;
  background-color: rgb(229.5, 115.2, 18.9);
}
.hero__btn {
  width: fit-content;
  margin: 2rem auto 0;
  line-height: 1.15;
}

.formulario {
  background-color: #f2f2f2;
  padding: 5rem 0;
}
.formulario__titulo {
  text-align: center;
  margin: 0;
}
.formulario__contenido {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .formulario__contenido {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 3rem;
  }
}
.formulario__campo {
  padding: 1.5rem 1rem;
  border: 1px solid #c7c3c8;
  min-width: 0;
}
.formulario__btn {
  background-color: #b8336a;
  font-size: 2rem;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFF;
  text-decoration: none;
  border: none;
  margin-top: 2rem;
  padding: 1.5rem 4rem;
  text-transform: unset;
  font-weight: 700;
  transition: 0.3s background-color;
}
.formulario__btn:hover {
  cursor: pointer;
  background-color: rgb(229.5, 115.2, 18.9);
}
.formulario__btn {
  margin-top: 0;
  min-width: 0;
}

.eventos {
  padding: 5rem 0;
}
.eventos span {
  text-transform: capitalize;
}
.eventos__contenido {
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .eventos__contenido {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.eventos__card {
  display: block;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .eventos__card {
    margin-bottom: 0;
    display: flex;
    flex-direction: column;
  }
}
.eventos__img {
  width: 100%;
}
.eventos__info {
  border: 1px solid #f2f2f2;
  padding: 2rem;
  flex: 1;
}
.eventos__fecha {
  margin: 0;
  font-size: 1.4rem;
}
.eventos__titulo {
  font-family: "Staatliches", cursive;
  font-size: 4rem;
  margin: 1rem 0;
  text-transform: uppercase;
  font-weight: 700;
}
.eventos__title {
  font-family: "Raleway", sans-serif;
  font-size: 3rem;
  font-weight: 700;
  margin: 1rem 0;
}

.perfil {
  display: flex;
  align-items: center;
  margin-top: 2rem;
  gap: 2rem;
}
.perfil__img {
  border-radius: 50%;
  width: 10rem;
}
.perfil__title {
  font-family: "Raleway", sans-serif;
  font-size: 1.6rem;
  font-weight: 700;
  margin: 0.5rem 0;
  line-height: 1.8;
}
.perfil__p {
  margin: 0.5rem 0;
}